


.navbar {
    position: fixed;
    top: 30px;
    right: 50%;
    left: 50%;
    margin-left: -200px;
    z-index: 2;
    will-change: transform;
}

.navbar__container {
    display: flex;
    align-items: center;
    background-color: #463e3e22;
    border-radius: 50px;
    /* padding: 20px; */
    width: 400px;
    height: 80px;
    margin: 0 auto;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    box-shadow: 0px 0px 94px 3px rgba(0,0,0,0.48);;
}

.navbar__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    height: 100%;
    z-index: 1;
}

.navbar__list-main {
    padding: 0 20px;
}

.navbar__list-main:last-child {
    margin-left: 1px;
}

.navbar__list-main a {
    color: rgb(50, 40, 40);
}

.navbar__links {
    text-decoration: none;
    transition: all .1s ease-in-out;
    display: flex;
    align-items: center;
}

.navbar__links:hover {
    opacity: 0.8;
    transition: all 0.3s
}

.active-btn {
    /* top: 26px; */
    /* height: 28px; */
    height: 0;
    padding: 15px;
    width: 74px;
    border-radius: 50px;
    /* margin: 0 0 0 79px; */
    transform: translateX(79px);
    position: absolute;
    /* z-index: -1; */
    transition: all 0.3s;
    background: -webkit-linear-gradient(0deg, rgb(182, 226, 216) 60%, rgb(164, 221, 222) 90%);
    background: -moz-linear-gradient(0deg, rgb(182, 226, 216) 60%, rgb(164, 221, 222) 90%);
    background: linear-gradient(0deg, rgb(182, 226, 216) 60%, rgb(164, 221, 222) 90%);
    box-shadow: 0px 0px 32px 10px rgba(164, 221, 222, 0.778);
    will-change: transform;
}


@media only screen and (max-width: 401px) {
    .navbar__container{
        border-radius: 0 0 50px 50px;
    }
    .navbar {
        top: 0;
    }
}