
.footer {
	margin-top: auto;
	padding: 15px 0 15px;
	background-color: rgb(73, 62, 53);
    color: aliceblue;
}

.footer__wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 27px;
}

.social {
	display: flex;
	column-gap: 30px;
	align-items: center;
}

.copyright {
	font-size: 16px;
}

.copyright p + p {
	margin-top: 0.5em;
}