@font-face {
    font-family: OstrichSans;
    src: url(../fonts/OstrichSans-Heavy.otf);
}


* {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

#root {
    min-height: 100vh;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}


body {
    margin: 0;
    background-color: #544747db;
}

a {
    text-decoration: none;
}

h1,h2,h3,h4 {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
    padding: 0;
}
p {
    margin: 0;
    padding: 0;
}

