 */


#section {
    font-family: OstrichSans;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 160px;
    padding: 35px;
}

.projects__title {
    font-size: 60px;
    padding-top: 100px;
}

.projects__container {
    display: flex;
    justify-content: center;
    font-family: OstrichSans;
}

.projects {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    row-gap: 30px;
    column-gap: 30px;
}

.projects > :last-child {
    grid-column: span 2;
}


.navbar {
    font-family: OstrichSans;
}

.header__info {
    font-family: OstrichSans;
}

.footer {
    font-family: OstrichSans;
}

.about {
    font-family: OstrichSans;
}

.container {
    top: 80px;
    padding: 70px 0;
    padding-top: 120px;
    font-family: OstrichSans;
}

.container-project {
    margin: 0 auto;
	padding: 0 15px;
}

.project-main {
    margin: 0 auto;
	max-width: 865px;

	display: flex;
	flex-direction: column;
	align-items: center;

	text-align: center;
}
.project-main-img {
    max-width: 100%;
	margin-bottom: 40px;

    border-radius: 10px;
}



@media only screen and (max-width: 910px) {
    .projects {
        grid-template-columns: repeat(1, 1fr);
    }
    .projects > :last-child {
        grid-column: auto;
    }
}